import React, { Component } from "react";

import theme from "constants/theme";
import Divider from "components/generic/Divider";
import PageSection from "components/generic/PageSection";
import ProjectCard from "components/Projects/ProjectCard";

import apolloBlogImg from "media/apollo-graphql-blog.PNG";

const Projects = () => {
  return (
    <div>
      <PageSection
        color={theme.colors.gray2}
        paddingBottom="50"
        paddingTop="50"
      >
        <Divider centered={true} big={true}>
          <h2>Projects</h2>
        </Divider>
        <ProjectCard
          title="React Article Manager"
          image={apolloBlogImg}
          description="Blogging Platform built using React, Express.js, Graphql and
            MongoDB. Features authentication with Auth0, markup blog editor, and
            individual user profiles."
        />
        <ProjectCard
          title="React Article Manager"
          image={apolloBlogImg}
          description="Blogging Platform built using React, Express.js, Graphql and
            MongoDB. Features authentication with Auth0, markup blog editor, and
            individual user profiles."
        />
        <ProjectCard
          title="React Article Manager"
          image={apolloBlogImg}
          description="Blogging Platform built using React, Express.js, Graphql and
            MongoDB. Features authentication with Auth0, markup blog editor, and
            individual user profiles."
        />
      </PageSection>
    </div>
  );
};

export default Projects;



